<template>
  <div>
    <div class="topTitle">
      <span class="topTitle-span1">机器学习</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">AML预测结果</span>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>查询条件
        </div>
        <div class="contentCellBody">
          <Form :model="formValidate" :label-width="120" inline>
            <Row>
              <FormItem label="预测日期" prop="username">
                <DatePicker :value="formValidate.warn_date" @on-change="formValidate.warn_date=$event"
                 format="yyyyMMdd" type="daterange" placeholder="选择日期" style="width: 200px"></DatePicker>
              </FormItem>
            </Row>
            <Row>
                <div class="btnClass">
                  <Button type="primary" @click="doSearch()">查询</Button>
                </div>
              </Row>
          </Form>
        </div>
      </div>
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>数据列表
        </div>
        <div class="contentCellBody">
          <Row>
            <table class="tableBox">
              <thead>
                <tr>
                  <td v-for="(item, index) in tableHeader">{{item.name}}</td>
                </tr>
              </thead>
              <tbody>
                <template v-for="(item,index) in tableBody">
                <tr>
                  <td>{{pageSize*(pageNo-1)+index+1}}</td>   
                  <td>{{item.warn_date}}</td>
                  <td>{{item.mbrc}}</td>
                  <td>{{item.csnm}}</td>
                  <td>{{item.ctnm}}</td>
                  <td>{{item.prediction}}</td>
                </tr>
                </template>
              </tbody>
            </table>
          </Row>
          <div class="flexCenter pageBox">
            <Page :current.sync="pageNo" :total="rowCount" @on-change="doPageNo" :page-size="pageSize" @on-page-size-change='doPageSize' show-elevator show-sizer/>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  components: {},
  created() {
    this.formValidate.warn_date[0] = this.getCurrDate();
    this.formValidate.warn_date[1] = this.formValidate.warn_date[0];
  },
  mounted() {
    this.doSearch();
  },
  computed: {},
  watch: {},
  data() {
    return {
      pageNo: 1,
      rowCount: 0,
      pageSize:10,
      tableHeader: [
        { name: "序"},
        { name: "预测日期"},
        { name: "所属部门"},
        { name: "客户号"},
        { name: "客户名称"},
        { name: "预测结果"},
       ],
      tableBody: [
      ],
      formValidate: {
        warn_date: ['20190101','20190101'], 
        pageSize: 10,
        pageNo: 1
      },
      ruleValidate: {}
    };
  },
  methods: {
    doSearch(){
      let data = {
        warn_date1: this.formValidate.warn_date[0],
        warn_date2: this.formValidate.warn_date[1],
        pageSize: this.pageSize,
        pageNo: this.pageNo
      };
      //data = JSON.stringify(data);
      this.axios
        .post("/prediction/amlCtif", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          this.rowCount = response.data.page.rowCount;
          this.tableBody = response.data.datas;           
        })
        .catch(error => {});
    },
    doPageSize(iSize){
      this.pageSize = iSize*1;
      if (this.pageNo == 1){
        this.doSearch();
      }
    },
    doPageNo(iNo){
      this.pageNo = iNo*1;
      this.doSearch();
    },
    getCurrDate() {
		  var data = new Date();
	    var Da = new Date(data.getTime() + 24 * 60 * 60 * 1000 * 30);
	    var y = Da.getFullYear();
	    var m = Da.getMonth() + 1;
	    var d = Da.getDate();
	    m = m < 10 ? "0" + m : m;
	    d = d < 10 ? "0" + d : d;
	    return y + "" + m + "" + d;
		}
  }
};
</script>



